.header {
    min-height: 100px;
    background-color: red;
    display: flex;
    align-items: stretch;
    justify-content: flex-end;
    padding: 0 50px;
    box-shadow: 1px 1px 10px #777;
  }
  
  .nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: stretch;
    height: 100%;
  }
  
  .nav li {
    display: flex;
    align-items: center;
  }
  
  .nav a {
    color: #fff;
    text-decoration: none;
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
    padding: 0 20px;
    text-shadow: 2px 2px 2px #66645F;
  }
  
  .nav a:hover,
  .nav a.active {
    background-color: rgba(0,0,0,.2);
  }